
























































/**
 *
 * @author fqq
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";

import Url from "../../../api";

@Component({
  components: {}
})
export default class AddApplication extends Vue {
  @Prop({ type: String, default: "添加应用分类" })
  dialogTitle?: string;

  @Prop({ type: Boolean, default: false })
  dialogVisible!: boolean;

  @Prop({ type: Object, default: () => ({}) })
  data!: any;

  @Watch("dialogVisible")
  handleVisible(newValue: boolean) {
    this.addDialog = newValue;
    if (newValue) {
      this.$nextTick(() => {
        let ref: any = this.$refs.addForm;
        ref.resetFields();
        this.addForm = JSON.parse(JSON.stringify(this.data));
      });
    }
  }

  mobileType = [];
  elementIcon: Array<any> = [
    "el-icon-info",
    "el-icon-error",
    "el-icon-success",
    "el-icon-warning",
    "el-icon-question",
    "el-icon-back",
    "el-icon-arrow-left",
    "el-icon-arrow-down",
    "el-icon-arrow-right",
    "el-icon-arrow-up",
    "el-icon-caret-left",
    "el-icon-phone",
    "el-icon-caret-bottom",
    "el-icon-caret-top",
    "el-icon-caret-right",
    "el-icon-d-arrow-left",
    "el-icon-d-arrow-right",
    "el-icon-minus",
    "el-icon-plus",
    "el-icon-remove",
    "el-icon-circle-plus",
    "el-icon-remove-outline",
    "el-icon-circle-plus-outline",
    "el-icon-close",
    "el-icon-check",
    "el-icon-circle-close",
    "el-icon-circle-check",
    "el-icon-circle-close-outline",
    "el-icon-circle-check-outline",
    "el-icon-zoom-out",
    "el-icon-zoom-in",
    "el-icon-d-caret",
    "el-icon-sort",
    "el-icon-sort-down",
    "el-icon-sort-up",
    "el-icon-tickets",
    "el-icon-document",
    "el-icon-goods",
    "el-icon-sold-out",
    "el-icon-news",
    "el-icon-edit-outline",
    "el-icon-rank",
    "el-icon-refresh",
    "el-icon-share",
    "el-icon-setting",
    "el-icon-message",
    "el-icon-date",
    "el-icon-printer",
    "el-icon-time",
    "el-icon-bell",
    "el-icon-mobile-phone",
    "el-icon-phone-outline",
    "el-icon-picture",
    "el-icon-picture-outline",
    "el-icon-delete",
    "el-icon-search",
    "el-icon-edit",
    "el-icon-service",
    "el-icon-view",
    "el-icon-menu",
    "el-icon-more",
    "el-icon-more-outline",
    "el-icon-star-on",
    "el-icon-star-off",
    "el-icon-location",
    "el-icon-location-outline",
    "el-icon-upload",
    "el-icon-upload2",
    "el-icon-download",
    "el-icon-loading"
  ];

  addDialog = false;
  addForm: any = {
    id: "",
    name: "",
    icon: "",
    orderNum: "",
    mobileTypeId: "",
    homePage: "",
    remark: ""
  };
  formRules: any = {
    name: { required: true, trigger: "blur", message: "请输入名称" },
    orderNum: { required: true, trigger: "blur", message: "请输入序号" },
    mobileTypeId: { required: true, trigger: "blur", message: "请选择类型" },
    homePage: { required: true, trigger: "blur", message: "请输入主页地址" }
  };
  loading = false;

  getMobileTypeData() {
    this.$http
      .post(Url.getAPPOrganizeList, {
        current: 1,
        size: 100
      })
      .then((res: any) => {
        if (res.data.status) {
          this.mobileType = res.data.data.records;
        } else {
          this.$message.error(res.data.message);
        }
      });
  }

  closeDialog() {
    this.$emit("close");
  }

  saveSensorAttribute() {
    let rest: any = this.$refs.addForm;
    rest.validate((valid: any) => {
      if (valid) {
        this.loading = true;
        let url = this.addForm.id ? Url.updateAPPManagement : Url.addAPPManagement;
        if (typeof this.addForm.remark === "string") {
          this.addForm.remark = this.addForm.remark.trim();
        }
        this.$http
          .post(url, this.addForm)
          .then((res: any) => {
            this.loading = false;
            if (res.data.status) {
              this.$message.success(this.addForm.id ? "编辑成功" : "添加成功");
              this.$emit("submit");
            } else {
              this.$message.error(res.data.message);
            }
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        return false;
      }
    });
  }

  created() {
    this.getMobileTypeData();
  }
}
