













































































/**
 *
 * @author
 */
import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import AddApplication from "./components/AddApplication.vue";

import Url from "../../api";
import utils from "@/utils/utils";

@Component({
  components: {
    AddApplication
  }
})
export default class AppManagement extends Vue {
  tableForm = {
    name: "",
    orderNum: ""
  };
  loading = false;
  tableData = [];
  total = 0;
  pageNum = 1;
  pageSize = 10;

  dialogTitle = "";
  visibleDialog = false;
  formData = {
    id: "",
    name: "",
    icon: "",
    orderNum: "",
    mobileTypeId: "",
    homePage: "",
    remark: ""
  };

  getTableData() {
    this.loading = true;
    this.$http
      .post(Url.getAPPManagementList, {
        query: this.tableForm,
        current: this.pageNum,
        size: this.pageSize
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.tableData = res.data.data.records;
          this.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  search() {
    this.pageNum = 1;
    this.getTableData();
  }

  handleCurrentChange(val: number) {
    this.pageNum = val;
    this.getTableData();
  }

  handleSizeChange(val: number) {
    this.pageSize = val;
    this.getTableData();
  }

  handleAdd() {
    this.dialogTitle = "添加App应用";
    this.formData = {
      id: "",
      name: "",
      icon: "",
      orderNum: "",
      mobileTypeId: "",
      homePage: "",
      remark: ""
    };
    this.visibleDialog = true;
  }

  handleEdit(row: any) {
    this.dialogTitle = "编辑App应用";
    utils.mapValue(this.formData, row);
    this.visibleDialog = true;
  }

  handleAddSubmit(form: any) {
    this.search();
    this.visibleDialog = false;
  }

  closeDialog() {
    this.visibleDialog = false;
  }

  handleDelete(item: any) {
    let _this = this;
    this.$confirm("确认删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(Url.deleteAPPManagement + "/" + item.id).then((res: any) => {
          if (res.status) {
            if (res.data.status) {
              this.$message({
                type: "success",
                message: "删除成功!"
              });
              //重新获取列表
              _this.getTableData();
            } else {
              _this.$message({
                showClose: true,
                message: res.data.message,
                type: "error"
              });
            }
          }
        });
      })
      .catch(() => {});
  }

  created() {
    this.getTableData();
  }
}
